import {BiValue} from "core/ui/common/positions"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {Txt} from "core/ui/controls/txt"
import {Fonts, Images} from "games/speedread/ticker/assets"
import {Task} from "../logic"

const count = 9

type Props = {
  onComplete: () => {}
}

const durations = {
  startDelay: 500,
}

const biValues = BiValue.create({
  angle: 0,
}, {
  angle: 90,
})

export class ReadComponent extends GameComponent<Props> implements OnInit {

  positions = Sizes.create({
    words: {y: -30}
  })
  biPositions = Sizes.bi({
    margin: {x: 350, y: 200, absolute: true},
    bg: {
      angle: 0,
      scale: 1
    }
  }, {
    margin: {x: 350, y: 100, absolute: true},
    bg: {
      angle: 90,
      scale: 0.8
    }
  })

  card = this.addImage(Images.white).anchorCenter()

  text = this.add(new Txt("", Fonts.word)).anchorCenter()

  unpauseCallback?: () => void

  // bgAnimation(word: string){
  //   const text = new TextOnCenterBehavior(Fonts.word)
  //   const cardLeft = this.addImage(Images.white).anchorCenter()
  //     .addBehavior(text)
  //   const cardRight = this.addImage(Images.white).anchorCenter()
  //     .updatePos(screenSizeService.screenSize.width, 0)
  //   this.bringToTop(this.card)
  //   this.bringToTop(this.text)
  //   Tweens.start({
  //     to: {x: -cardLeft.width},
  //     target: cardLeft,
  //     easing: Phaser.Easing.Sinusoidal.Out,
  //     duration: durations.move,
  //     onComplete: () => cardLeft.destroy()
  //   })
  //   Tweens.start({
  //     to: {x: 0},
  //     target: cardRight,
  //     easing: Phaser.Easing.Sinusoidal.In,
  //     duration: durations.move,
  //     onComplete: () => cardRight.destroy()
  //   })
  // }

  onNewTask(task: Task): void {
  }

  showAllText() {
    this.disableInput(true)
    this.showWord(this.getCurrentTask(), 0)
  }

  showWord(task: Task, index: number) {
    const delay = 60 * 1000 / task.speed
    if (index >= task.textWords.length) {
      this.delay(durations.startDelay).addOnce(() => {
        this.disableInput(false)
        this.props.onComplete()
      })
      return
    } else {
      this.text.text = task.textWords[index]
      this.text.updatePivot()
      const callback = () => {
        this.delay((index == 0 && delay < durations.startDelay ? durations.startDelay : delay)).addOnce(() =>
          this.showWord(task, index + 1))
      }
      if (this.isGamePaused) {
        this.unpauseCallback = callback
      } else {
        callback()
      }
    }
  }

  onGamePauseChanged(isPaused: boolean): void {
    if (!isPaused && this.unpauseCallback) {
      this.unpauseCallback()
      this.unpauseCallback = undefined
    }
  }

  onInit(): void {
    this.updatePivot()
  }

}



