import {createLevelsFromArray} from "core/types/logic"
import * as _ from 'lodash'

export interface Level {
  speed: number
}

function level(speed: number): Level {
  return {speed}
}

export const levels = createLevelsFromArray(_.range(20).map(x => level(100 + x * 50)))



