import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/index"
import {OnInit} from "core/ui/components/onInit"
import {UniqSeq} from "core/utils/circleSeq"
import {RandomGenerator} from "core/utils/randomGenerator"
import {tuple} from "core/utils/tuples"
import {Images} from "games/speedread/ticker/assets"

const durations = {
  puz: 1000,
  delay: 300
}

export class IntroComponent extends GameComponent implements OnInit {
  positions = Sizes.create({
    puz: [
      tuple(400, 300, "0"),
      tuple(700, 100, "3"),
      tuple(1200, 300, "2"),
      tuple(1700, 200, "4"),
      tuple(1600, 600, "2"),
      tuple(1500, 1100, "4"),
      tuple(1000, 1000, "1"),
      tuple(600, 1200, "3"),
      tuple(500, 600, "2"),
      tuple(300, 900, "2"),
      tuple(900, 500, "0"),
      tuple(1300, 700, "1"),
    ]
  })

  bg = this.addImage(Images.bg)
  puzSeq = new UniqSeq(1)
  rng = new RandomGenerator()

  puzList = this.positions.puz.map(([x, y, puz]) =>
    this.addImage(Images.puz(puz)).anchorCenter().updatePos(x, y))

  // .fluentUpdate(x => x.alpha = this.rng.float()))

  onInit(): void {
    this.puzList.map(x => {
      return Tweens.start({
        target: x,
        to: {alpha: 0},
        simpleYoyo: true,
        repeat: -1,
        duration: this.rng.int(durations.puz, durations.puz / 2),
      })
    })
  }

}




