import {GameLogic} from "core/logic/index"
import {Sys} from "core/logic/sys"
import {UniqSeq} from "core/utils/circleSeq"
import {RandomGenerator} from "core/utils/index"
import {Strings} from "core/utils/strings"
import {TickerData} from "games/speedread/ticker/data"
import {Level} from "./levels"

const tutorialSpeed = 50

export type Task = {
  speed: number
  textIndex: number,
  blockIndex: number,
  lastBlock: boolean,
  textWords: string[]
  question: {
    text: string
    answers: string[]
  }
  answers: boolean[]
}

export class Logic extends GameLogic<Task, Level> {
  rng = new RandomGenerator()
  textSeq = new UniqSeq(TickerData.data.length)

  nextTask(level: Level) {
    if (this.isTutorial && this.taskIndex > 0) {
      return undefined
    }
    const prevTask = this.getPrevTask()
    const speed = this.isTutorial ? tutorialSpeed : level.speed
    const [textIndex, blockIndex] = prevTask && !prevTask.lastBlock
      ? [prevTask.textIndex, prevTask.blockIndex + 1]
      : [this.textSeq.next(), 0]
    const text = this.isTutorial ? TickerData.tutorialText : TickerData.data[textIndex]
    const lastBlock = text.blocks.length <= blockIndex + 1
    const block = text.blocks[blockIndex]
    const textWords = block.text.split(/\n|\s/).map(x => x.trim()).filter(x => /(\w|[А-Яа-я])+/.test(x))
    const questionData = this.rng.sample(block.questions)
    const wrong = this.rng.sample(Strings.splitTrim(questionData.wrong, ";"))
    const correct = questionData.correct
    const answer = this.rng.bool()
    const questionAnswers = answer ? [correct, wrong] : [wrong, correct]
    const answers = [answer]
    const question = {
      text: questionData.text,
      answers: questionAnswers
    }
    const task: Task = {
      lastBlock,
      blockIndex,
      speed,
      textIndex,
      textWords,
      question,
      answers
    }
    if (Sys.showLog) {
      console.log("task", task)
    }
    return task
  }

}





