import {Tweens} from "core/ui/animations/tweens"
import {BiValue} from "core/ui/common/positions"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {ImageBtn} from "core/ui/controls/btn"
import {Control} from "core/ui/controls/control"
import {Txt} from "core/ui/controls/txt"
import {Fonts, Images, Sounds} from "games/speedread/ticker/assets"
import {Task} from "../logic"

const durations = {
  buttons: 200
}

const biValues = BiValue.create({
  buttonScale: 1,
}, {
  buttonScale: 0.9,
})

export class QuestionComponent extends GameComponent implements OnInit {

  positions = Sizes.create({
    answerMarin: -100,
    btnTextWidth: 500,
  })
  biPositions = Sizes.bi({
    buttons: {x: 0, y: 500},
    buttonDistance: 400,
    wordWrap: 900,
    margin: {x: 590, y: 0, absolute: true},
  }, {
    buttons: {x: 0, y: 590},
    buttonDistance: 600,
    wordWrap: 700,
    margin: {x: 0, y: 300, absolute: true},
  })
  buttonsGroup = this.add(new Control()).anchorCenter().setPos(this.current(this.biPositions.buttons))
  bg = this.addImage(Images.vopros).anchorCenter()
  question = this.add(new Txt("", Fonts.question)).anchorCenter()
    .fluentUpdate(x => x.wordWrapWidth = this.current(this.biPositions.wordWrap))
  private buttons: ImageBtn[] = []

  onNewTask(task: Task): void {
    this.createButtons(task)
    this.question.alpha = 0
    this.buttons.forEach(x => x.textControl.alpha = 0)
  }

  showText() {
    this.question.alpha = 1
    this.buttons.forEach(x => x.textControl.alpha = 1)
  }

  tweenButtons(show: boolean) {
    this.buttonsGroup.setPos(this.current(this.biPositions.buttons))
    const buttons = Tweens.start({
      target: this.buttonsGroup,
      to: {y: this.buttonsGroup.y - this.current(this.biPositions.buttonDistance)},
      reverse: show,
      notStart: true,
      easing: Phaser.Easing.Sinusoidal.InOut,
      duration: durations.buttons,
      onComplete: () => {
        if (show) {
          this.answersAreReady()
        }
      }
    })
    return buttons
  }

  createButtons(task: Task) {
    this.buttonsGroup.removeAll(true)
    this.buttonsGroup.symmetricScale = this.current(biValues.buttonScale)
    this.question.text = task.question.text
    this.question.updatePivot()
    this.buttons = task.question.answers.map((word, i) => {
      const btn = new ImageBtn(word, this.gameKey(Images.otvet_2), () => {
      }, {...Fonts.question, wordWrap: true, wordWrapWidth: this.positions.btnTextWidth})
      btn.onHover(out => {
        if (!out) {
          this.playSound(Sounds.navedenie)
        }
        return btn.key = this.gameKey(out ? Images.otvet_2 : Images.otvet_1)
      })
      return this.buttonsGroup.add(this.addAnswerBehavior(btn,
        i == 0, {dark: true, margin: this.positions.answerMarin, location: this.isPortrait ? "left" : "bottom"}))
    })
    if (this.isPortrait) {
      Sizes.tableColumn(this.buttons, this.current(this.biPositions.margin))
    } else {
      Sizes.tableRow(this.buttons, this.current(this.biPositions.margin))
    }
    this.buttonsGroup.updatePivot()
    this.updatePivot()
    if (this.isFirstTask) {
      this.tweenButtons(false).start()
    }
  }

  onInit(): void {
  }

}



