import {Signals} from "core/ui/animations/signals"
import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {BaseMainGameComponent} from "core/ui/components/games/baseMainGameComponent"
import {TutorialTextComponent} from "core/ui/components/games/gameUI/tutorialTextComponent"
import {OnInit} from "core/ui/components/onInit"
import {Control} from "core/ui/controls/control"
import {ControlUtl} from "core/ui/controls/controlUtl"
import {Txt} from "core/ui/controls/txt"
import {UniqSeq} from "core/utils/circleSeq"
import {RandomGenerator} from "core/utils/randomGenerator"
import {tuple} from "core/utils/tuples"
import {IntroComponent} from "games/speedread/ticker/components/introComponent"
import {QuestionComponent} from "games/speedread/ticker/components/questionComponent"
import {ReadComponent} from "games/speedread/ticker/components/readComponent"
import {Fonts, Images, Sounds, Translations} from "../assets"
import {Task} from '../logic'

const durations = {
  mainGroup: 500,
  question: 200,
  intro: 1000,
  readDelay: 1000,
  readDelayTutorial: 3000
}

export class MainComponent extends BaseMainGameComponent implements OnInit {
  positions = Sizes.create({
    questionTo: {x: Sizes.widthNormal / 2, y: 830},
    puz: [
      tuple(400, 300, "0"),
      tuple(700, 100, "3"),
      tuple(1200, 300, "2"),
      tuple(1700, 200, "4"),
      tuple(1600, 600, "2"),
      tuple(1500, 1100, "4"),
      tuple(1000, 1000, "1"),
      tuple(600, 1200, "3"),
      tuple(500, 600, "2"),
      tuple(300, 900, "2"),
      tuple(900, 500, "0"),
      tuple(1300, 700, "1"),
    ]
  })
  biPositions = Sizes.bi({
    questionFrom: {x: Sizes.widthNormal / 2, y: 650},
    speed: {x: Sizes.widthNormal / 2, y: 210},
  }, {
    questionFrom: {x: Sizes.widthNormal / 2, y: 670},
    speed: {x: Sizes.widthNormal / 2, y: 110},
  })

  rng = new RandomGenerator()
  puzSeq = new UniqSeq(1)

  bg = this.addImage(Images.bg)
  puzList = this.positions.puz.map(([x, y, puz]) =>
    this.addImage(Images.puz(puz)).anchorCenter().updatePos(x, y))

  mainGroup = this.add(new Control())
  tutorial: TutorialTextComponent

  speedText = this.mainGroup.add(new Txt("", Fonts.speed))
    .anchorCenter()
    .setPos(Sizes.center())

  private read: ReadComponent
  private question: QuestionComponent

  showRead(visible: boolean) {
    if (this.tutorial && !visible) {
      this.tutorial.setProps(this.translate(Translations.tutor2))
    }

    if (this.isFirstTask && visible) {
      return
    }

    const run = () => {

      this.playSound(Sounds.slide)
      const [start, end] = visible ? [0, -Sizes.width] : [-Sizes.width, 0]
      this.mainGroup.x = start
      Tweens.start({
        target: this.mainGroup,
        to: {x: end},
        reverse: true,
        easing: Phaser.Easing.Sinusoidal.InOut,
        duration: durations.mainGroup,
        onComplete: () => {
          if (!visible) {
            this.showSpeed(true)
            const start = this.tweenQuestion(true)
            start.chain(this.question.tweenButtons(true))
            start.start()
          } else {
            this.startRead()
          }
        }
      })
    }

    if (visible) {
      const end = this.tweenQuestion(false)
      this.question.tweenButtons(false).chain(end).start()
      end.onComplete.addOnce(() => {
        run()
      })
    } else {
      this.question.showText()
      run()
    }

  }

  tweenQuestion(show: boolean) {
    this.question.y = this.current(this.biPositions.questionFrom).y
    return Tweens.start({
      target: this.question,
      to: {y: this.positions.questionTo.y},
      reverse: show,
      notStart: true,
      easing: Phaser.Easing.Sinusoidal.InOut,
      duration: durations.question,
    })

  }

  introAnimation(): { start: () => void; onComplete: Phaser.Signal } {
    const intro = this.add(this.createComponent(IntroComponent))
    return {
      start: () => {
      },
      onComplete: Signals.bind(this.delay(durations.intro), () => {
        return ControlUtl.hideAndDestroy(intro)
      })
    }
  }

  onInit(): void {
    this.statusOptions.waitCompleteBeforeNewTask = true
    this.introOptions.runOnNewTaskAfter = true

    this.read = this.mainGroup.add(this.createComponentWithProps(ReadComponent,
      {onComplete: () => this.showRead(false)})).anchorCenter()
      .setPos(Sizes.center()).fluentUpdate(x => x.visible = false)

    this.question = this.mainGroup.add(this.createComponent(QuestionComponent)).anchorCenter()
    // .setBiPos(BiValue.map(this.biPositions.questionFrom, p => ({...p, x: p.x + Sizes.width}) ) )
      .updatePos(Sizes.w2 + Sizes.width, this.positions.questionTo.y)

    this.startIntro()

    if (this.isTutorial) {
      this.tutorial = this.add(this.createComponent(TutorialTextComponent).make())
    }
  }

  showSpeed(visible: boolean) {
    this.speedText.visible = visible
    this.read.visible = !visible
  }

  startRead() {
    const delay = this.isTutorial ? durations.readDelayTutorial : durations.readDelay
    this.disableInputOn(this.delay(delay)).addOnce(() => {
      this.showSpeed(false)
      this.playSound(Sounds.slide_kard)
      this.read.showAllText()
    })
  }

  onNewTask(task: Task) {
    if (this.tutorial) {
      this.tutorial.setProps(this.translate(Translations.tutor1))
    }

    this.speedText.text = this.translate(Translations.speed) + " " + task.speed
    this.speedText.updatePivot()
    if (this.isFirstTask) {
      this.startRead()
    } else {
      this.showRead(true)
    }
  }

}




